// jestSetup.js

// Setup officiel de react-native-gesture-handler
import "react-native-gesture-handler/jestSetup";

// Mock du module Animated pour éviter les warnings liés aux animations
jest.mock("react-native/Libraries/Animated/NativeAnimatedHelper");

// Mock basique de react-navigation pour éviter les erreurs liées à la navigation
jest.mock("@react-navigation/native", () => {
  const actualNav = jest.requireActual("@react-navigation/native");
  return {
    ...actualNav,
    useNavigation: () => ({
      navigate: jest.fn(),
      goBack: jest.fn(),
    }),
  };
});

// Optional: mock de modules supplémentaires si besoin
jest.mock("react-native-reanimated", () => require("react-native-reanimated/mock"));
